import React, { useState } from "react";

import "./index.scss";
import plusIcon from "../../assets/icons/plus.png";

const initialQuestions = [
  {
    id: 1,
    question: "Care este dresscode-ul evenimentului?",
    answer: "Unicul dresscode potrivit este dispoziia ta bun! ",
    expanded: false,
  },
  {
    id: 2,
    question: "Ce cadouri pot s aduc la ntmpinare?",
    answer:
      "Ne-am bucura dac n locul tradiionalului buchet de flori, vei aduce o sticl de vin preferat sau o carte care te-a marcat. ",
    expanded: false,
  },
  {
    id: 3,
    question: 'Va fi pstrat tradiia "dezbrcatului miresei"?',
    answer:
      'Avnd n vedere stilul modern al nunii i dorina noastr de a ne asigura c evenimentul este plin de distracie i savoare, am decis c nu vom pstra tradiia "dezbrcatului miresei". ',
    expanded: false,
  },
  {
    id: 4,
    question: "Tierea tortului semnific ncheierea evenimentului?",
    answer:
      "Momentul tierii tortului nu marcheaz sfritul evenimentului. Dimpotriv, adevrata distracie urmeaz dup acest moment special, oferindu-ne oportunitatea de a ne bucura de fiecare secund a petrecerii. ",
    expanded: false,
  },
];

const Questions = () => {
  const [questions, setQuestions] = useState(initialQuestions);

  const handleClick = (id) => {
    const question = questions.find((question) => question.id === id);
    const newState = { ...question, expanded: !question.expanded };
    const newQuestions = questions.map((question) =>
      question.id === id ? newState : question
    );

    setQuestions(newQuestions);
  };

  return (
    <div className="questions_container">
      {questions.map((question) => {
        return (
          <div className="question_block" key={`${question.id}`}>
            <div className="question_section">
              <h4 className="question">{question.question}</h4>
              <div
                className={`expand_button ${
                  question.expanded ? "clicked" : ""
                }`}
                onClick={() => handleClick(question.id)}
              >
                <img src={plusIcon} alt="expand" className="plus_icon" />
              </div>
            </div>
            <div
              className={`answer_section ${
                question.expanded ? "selected" : ""
              }`}
            >
              <p>{question.answer}</p>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default Questions;
